import React, {useState, useEffect, Suspense} from 'react';
import {
  Switch,
  Route,
  Link
} from "react-router-dom";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";

import avatar from "assets/img/faces/marc.jpg";
import { useParams } from 'react-router-dom';

const RegisterFiche = React.lazy(() => import("./Reception/RegisterFiche"));

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  }
};

const useStyles = makeStyles(styles);

export default function UserProfile({addCallBack, deleteCallBack, webSocket}) {
  const classes = useStyles();
  const {patientId} = useParams();
  const [profil, setProfil] = useState(null)

  useEffect(() => {
      webSocket.send(JSON.stringify({
          type: 'get_patient_profile',
          content: {
            patient_id: patientId
          }
          })
      )
      
      addCallBack({
        id: 'UserProfile',
        type: [
          'patient_profile'
        ],
  
        run: (data) => {
          console.log('je suis la depuis UserProfile')
          console.log(data)
          if (data.type === 'patient_profile' && data.status !== 'error'){
            setProfil(data.content)
          }
        }
      })
      return () => {
        deleteCallBack('UserProfile')
      }
  }, [])

  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={7}>
          <Card>
            <CardHeader color="rose">
              <h4 className={classes.cardTitleWhite}>Liste des fiches
            {patientId}</h4>
              <p className={classes.cardCategoryWhite}>pour ce patient</p>
            </CardHeader>
            <CardBody>
              
      <Suspense fallback={<Loader/>}>
                    <Switch>
                        <Route path={`/lax_medic/work-place/patient-profiles-${patientId}/enregistrer-une-fiche`}>
                          <RegisterFiche addCallBack={addCallBack} deleteCallBack={deleteCallBack} webSocket={webSocket}/>
                        </Route>
                        <Route path="">
              <h5>Fiches</h5>
              <p>{profil ? JSON.stringify(profil.fiches) : '...' }</p>
              <br />
              
              <Link to={`/lax_medic/work-place/patient-profiles-${patientId}/enregistrer-une-fiche`}>
              <Button color="rose">Créer une nouvelle fiche</Button>
              </Link>
                        </Route>
                    </Switch>
      </Suspense>
            </CardBody>
            <CardFooter>
              <Button color="primary">Update Profile</Button>
            </CardFooter>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={5}>
          <Card profile>
            <CardAvatar profile>
              <a href="#pablo" onClick={e => e.preventDefault()}>
                <img src={avatar} alt="..." />
              </a>
            </CardAvatar>
            <CardBody profile>
              <h6 className={classes.cardCategory}>Patient</h6>
              <h4 className={classes.cardTitle}>{ profil ? profil.username + ' '+ profil.last_name + ' '+ profil.first_name : '. . .'}</h4>
              <p className={classes.description}>
              <table>
              <tbody>
              <tr>
              <th>
                Identifiant
              </th>
              <td><p>{ profil ? profil.id : '...' }</p></td>
              </tr>
              <tr>
              <th>
                Sex
              </th>
              <td><p>{ profil ? profil.sex : '...' }</p></td>
              </tr>
              <tr>
              <th>
                Né
              </th>
              <td><p>{ profil ? profil.birth_date : '...' }</p></td>
              </tr>
              <tr>
              <th>
                Adresse
              </th>
              <td><p>{ profil ? profil.adress : '...' }</p></td>
              </tr>
              </tbody>
              </table>
              </p>
              <Button color="primary" round>
                Modifier
              </Button>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}

const Loader=()=>{
  return (
    <div>
      loading ...
    </div>
  )
}