// @material-ui/icons
import Home from "@material-ui/icons/HomeWorkTwoTone";
import CaisseIcon from "@material-ui/icons/AttachMoneyTwoTone";
import PharmaIcon from "@material-ui/icons/LocalHospitalTwoTone";
// core components/views for Admin layout
import HomePage from "views/Home/Home.js";
import Reception from "workViews/Reception/Reception.js";
import PatientProfile from "workViews/PatientProfile"
// core components/views for RTL layout

const workViewsRoutes = [
  {
    path: "/patient-profiles-:patientId",
    name: "profil",
    rtlName: "لوحة القيادة",
    icon: PharmaIcon,
    component: PatientProfile,
    layout: "/lax_medic/work-place"
  },
  {
    path: "/reception",
    name: "Reception",
    rtlName: "لوحة القيادة",
    icon: Home,
    component: Reception,
    layout: "/lax_medic/work-place"
  },
  {
    path: "/caisse",
    name: "Caisse",
    rtlName: "لوحة القيادة",
    icon: CaisseIcon,
    component: HomePage,
    layout: "/lax_medic/work-place"
  },
];

export default workViewsRoutes;
