import React from "react";
import { createBrowserHistory } from "history";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  useHistory,
  useLocation
} from "react-router-dom";

import { makeStyles } from "@material-ui/core/styles";
// @material-ui/core
import Icon from "@material-ui/core/Icon";
import Button from "@material-ui/core/Button";
// @material-ui/icons
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardIcon from "components/Card/CardIcon.js";
import CardFooter from "components/Card/CardFooter.js";

import workViewsRoutes from "../../workViewsRoutes.js";
import PatientProfile from "../../workViews/PatientProfile"

import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";

const useStyles = makeStyles(styles);

const hist = createBrowserHistory();

export default function WorkPlace({addCallBack, deleteCallBack, webSocket}) {
    const classes = useStyles();
    return (
        <div> 
          {window.location.pathname=== "/lax_medic/work-place"
            ?       <GridContainer>
            {workViewsRoutes.map((prop, key) => {
                return (
                  <GridItem xs={12} sm={6} md={4} key={key}>
                    <Card>
                      <CardHeader color="warning" stats icon>
                        <CardIcon color="success">
                        <Icon><prop.icon/></Icon>
                        </CardIcon>
                        <p className={classes.cardCategory}>{prop.name}</p>
                        <h3 className={classes.cardTitle}>
                        {prop.name}
                        </h3>
                      </CardHeader>
                      <CardFooter stats>
                        <div className={classes.stats}>
                          <Link to={prop.layout + prop.path}>
                          <Button color="primary">
                            Entrer
                          </Button>
                          </Link>
                        </div>
                      </CardFooter>
                    </Card>
                  </GridItem>
                );
            })}  
            </GridContainer>
            : ''}     

      <Switch>{/* 
              <Route
                path={"/lax_medic/work-place/patient-profiles/:patientId"}
              >
                <PatientProfile addCallBack={addCallBack} deleteCallBack={deleteCallBack} webSocket={webSocket}/>
              </Route> */}
          {workViewsRoutes.map((prop, key) => {
              return (
                <Route
                  path={prop.layout + prop.path}
                  key={key}
                >
                  <prop.component addCallBack={addCallBack} deleteCallBack={deleteCallBack} webSocket={webSocket}/>
                </Route>
              );
          })}
        </Switch>
        
        </div>
    )
}
